<div class="w-100 d-flex justify-content-center"><div class="d-flex justify-content-center flex-grow-1 team-wrap" v-for="team in RoleArray" :style="[{'background-color': team.color}]"><div class="card-wrap card-background card-padding card-full-height d-flex justify-content-center" v-if="team.groupedRoles.single" v-for="item in team.groupedRoles.single" :class="item.color" :style="generateStyle(item)"><p class="ver-text card-title m-0">{{ item.label }}</p><p class="ver-text card-desc m-0" :class="item.color">{{ item.desc }}</p></div><div class="h-100 d-flex flex-column" v-if="team.groupedRoles.pair" v-for="pair in team.groupedRoles.pair"><div class="text-center" v-if="pair.icon"><i class="bi " :class="[pair.color,`bi-${pair.icon}`]"></i></div><div class="d-flex" :class="[pair.icon?&quot;h--30&quot;:&quot;h-100&quot;]"><div class="card-wrap card-background card-padding d-flex justify-content-center" v-for="item in pair.card" :class="item.color" :style="generateStyle(item)"><p class="ver-text card-title m-0">{{ item.label }}</p><p class="ver-text card-desc m-0" :class="item.color">{{ item.desc }}</p></div></div></div></div></div>